import * as React from 'react';
import { hasClass } from '../../utils/helpers';

interface IToggleProps{
    hideMain: () => void;
    showMain: () => void;
}

export class Toggle extends React.Component<IToggleProps,any>{
    render(){
        return(
            <div className="toggle" onClick={this.toggle.bind(this)} onTouchEnd={this.handleTouchEnd.bind(this)}></div>
        );
    }
    
    /**
	 * Toggles main wrapper.
	 */
    toggle() {
        if (hasClass(document.body, 'fullscreen')) {
            this.props.showMain();
        }
        else {
            this.props.hideMain();
        }
    }

    // Prevent event from bubbling up to "hide event on tap" event.
	handleTouchEnd(event: React.TouchEvent) {
        event.stopPropagation();
	}
} 