import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {IImage} from '../IImage';

interface IThumbnailProps {
    image: IImage;
    index: number;
    key: number;
    isActive: boolean;
    isLocked: boolean;
    onThumbnailClick: (index: number) => void;
}

export class Thumbnail extends React.Component<IThumbnailProps, any>{

    componentDidMount() {
        (ReactDOM.findDOMNode(this.refs['article']) as HTMLDivElement).focus();
    }

    render() {
        return (
            <article className={this.props.isActive ? "active" : ""} ref="article" onClick={this.handleClick.bind(this) }>
                    <a className="thumbnail" href={this.props.image.full} data-index={this.props.index}>
                        <img src={this.props.image.thumb} alt="" />
                        </a>
            </article>
        );
    }

    handleClick(event: React.MouseEvent) {
        // Stop other events.
        event.preventDefault();
        event.stopPropagation();
            
        // Locked? Blur.
        if (this.props.isLocked) {
            (ReactDOM.findDOMNode(this.refs['article']) as HTMLDivElement).blur();
        }
        else {
            this.props.onThumbnailClick(this.props.index);
        }
    }
}

