import './libs/responsive-tools/browser';
import { breakpoints } from './libs/responsive-tools/breakpoints';

import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { App } from './app/App';
import * as serviceWorker from './serviceWorker';
import { removeClass, addEvent, addClass } from './utils/helpers';

import './assets/styles/main.scss';
import { IImage } from './app/IImage';

// Breakpoints.
breakpoints({
  xlarge: ['1281px', '1680px'],
  large: ['981px', '1280px'],
  medium: ['737px', '980px'],
  small: ['481px', '736px'],
  xsmall: [null, '480px'],
});

const settings = {
  // Preload all images.
  preload: false,

  // Slide duration (must match "duration.slide" in _vars.scss).
  slideDuration: 500,

  // Layout duration (must match "duration.layout" in _vars.scss).
  layoutDuration: 750,

  // Thumbnails per "row" (must match "misc.thumbnails-per-row" in _vars.scss).
  thumbnailsPerRow: 2,

  // Side of main wrapper (must match "misc.main-side" in _vars.scss).
  mainSide: 'right',
};

// Remove is-loading-* classes on load.
window.onload = function () {
  removeClass(document.body, 'is-loading-0');

  window.setTimeout(function () {
    removeClass(document.body, 'is-loading-1');
  }, 100);

  window.setTimeout(function () {
    removeClass(document.body, 'is-loading-2');
  }, 100 + Math.max(settings.layoutDuration - 150, 0));
};

// Disable animations/transitions on resize.
let resizeTimeout: number;
addEvent(window, 'resize', (event) => {
  addClass(document.body, 'is-loading-0');
  window.clearTimeout(resizeTimeout);

  resizeTimeout = window.setTimeout(() => {
    removeClass(document.body, 'is-loading-0');
  }, 100);
});

fetch('https://raw.githubusercontent.com/SumGriVas/SumGriVas.github.io/data/images.json')
  .then((d) => d.json())
  .then((images: IImage[]) => {
    ReactDOM.render(
      <React.StrictMode>
        <App settings={settings} images={images} />
      </React.StrictMode>,
      document.getElementById('root')
    );
  });

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
