export function hasClass(el: HTMLElement, className: string) {
  if (el.classList) return el.classList.contains(className);
  else return !!el.className.match(new RegExp('(\\s|^)' + className + '(\\s|$)'));
}

export function addClass(el: HTMLElement, className: string) {
  if (el.classList) el.classList.add(className);
  else if (!hasClass(el, className)) el.className += ' ' + className;
}

export function removeClass(el: HTMLElement, className: string) {
  if (el.classList) el.classList.remove(className);
  else if (hasClass(el, className)) {
    var reg = new RegExp('(\\s|^)' + className + '(\\s|$)');
    el.className = el.className.replace(reg, ' ');
  }
}

/**
 * Add an Event Listener to the object or element
 *
 * object: the element or window object
 * type: resize, scroll (event type)
 * callback: the function reference
 */
export function addEvent(object: Element | Window, type: string, callback: EventListenerOrEventListenerObject) {
  if (object == null || typeof object == 'undefined') return;
  if (object.addEventListener) {
    object.addEventListener(type, callback, false);
  } else if ('attachEvent' in object) {
    (object as any).attachEvent('on' + type, callback);
  } else {
    (object as any)['on' + type] = callback;
  }
}
