import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {Header} from './Header';
import {Thumbnails} from './Thumbnails';
import {Footer} from './Footer';
import {Toggle} from './Toggle';
import {IImage} from '../IImage';
import { breakpoints } from '../../libs/responsive-tools/breakpoints';


interface IMainProps {
    images: IImage[],
    ref:any,
    current: number | null,
    isLocked: boolean,
    settings: any,
    onThumbnailClick: (index:number) => void,
    hideMain: () => void,
    showMain: () => void
}


interface IMainState {
    touchPosX: number,
    touchPosY: number
}


export class Main extends React.Component<IMainProps, IMainState>{
    render() {
        return (
            <div id="main" ref="main" onTouchStart={this.handleTouchStart.bind(this)} onTouchMove={this.handleTouchMove.bind(this)}>
                <Header/>
                <Thumbnails images={this.props.images} current={this.props.current} isLocked={this.props.isLocked} onThumbnailClick={this.props.onThumbnailClick}/>
                <Footer/>
                <Toggle hideMain={this.props.hideMain} showMain={this.props.showMain}/>
            </div>
        );
    }
    
    
    /**
     * Touch gestures.
     */
    handleTouchStart(event: React.TouchEvent<HTMLDivElement>) {
        // Bail on xsmall.
        if (breakpoints.active('<=xsmall')) return;

        // Record start position.
        this.setState({
            touchPosX: event.touches[0].pageX,
            touchPosY: event.touches[0].pageY
        });
    }
    
    
    /**
     * Touch gestures.
     */
    handleTouchMove(event: React.TouchEvent<HTMLDivElement>) {
        // Bail on xsmall.
        if (breakpoints.active('<=xsmall')) return;

        // No start position recorded? Bail.
        if (this.state.touchPosX === null
            || this.state.touchPosY === null)
            return;

        // Calculate stuff.
        let diffX = this.state.touchPosX - event.touches[0].pageX;
        let diffY = this.state.touchPosY - event.touches[0].pageY;
        let boundary = 20;
        let delta = 50;
        let result = false;

        // Swipe to close.
        switch (this.props.settings.mainSide) {
            case 'left':
                result = (diffY < boundary && diffY > (-1 * boundary)) && (diffX > delta);
                break;

            case 'right':
                result = (diffY < boundary && diffY > (-1 * boundary)) && (diffX < (-1 * delta));
                break;

            default:
                break;
        }

        if (result)
            this.props.hideMain();

        // Overscroll fix.
        let main = ReactDOM.findDOMNode(this.refs['main']) as HTMLDivElement;
        let th = main.offsetHeight;
        let ts = main.scrollHeight - main.scrollTop;

        if ((main.scrollTop <= 0 && diffY < 0) || (ts > (th - 2) && ts < (th + 2) && diffY > 0)) {
            event.preventDefault();
            event.stopPropagation();
        }

    }
    
} 