import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { Slide } from './Slide';
import { IImage } from '../IImage';
import { Toggle } from './Toggle';
import { breakpoints } from '../../libs/responsive-tools/breakpoints';

interface IViewerProps {
  image: IImage;
  width?: number;
  isLocked: boolean;
  onLock: (isLocked: boolean) => void;
  hideMain: () => void;
  showMain: () => void;
  handleNext: () => void;
  handlePrevious: () => void;
}

interface IViewerState {
  newImage?: IImage;
  currentImage: IImage;
  detachCurrentImage: boolean;
  touchPosX?: number;
  touchPosY?: number;
}

export class Viewer extends React.Component<IViewerProps, IViewerState> {
  constructor(props: any) {
    super(props);

    this.state = {
      currentImage: props.image,
      detachCurrentImage: false,
    };
  }

  shouldComponentUpdate(nextProps: IViewerProps, nextState: IViewerState) {
    // If component did't lock we will not rerender Viewer
    if (!nextProps.isLocked) {
      return false;
    }

    // console.log("\n shouldComponentUpdate");
    // console.log("|||||||||||||||||||||||| nextProps===this.props " + (nextProps === this.props));
    // console.log("|||||||||||||||||||||||| nextState.detachCurrentImage " + nextState.detachCurrentImage);
    // console.log("|||||||||||||||||||||||| this.state.detachCurrentImage " + this.state.detachCurrentImage);

    // Detach the current image
    if (nextState.detachCurrentImage) {
      // console.log("Detach the current image");
      return true;
    }

    // If we get a new image. We have to detach the current image.
    if (nextState.currentImage !== nextProps.image) {
      //  console.log("We have to detach the current image");
      this.setState({
        detachCurrentImage: true,
      });
      return false;
    }

    return true;
  }

  componentDidUpdate(prevProps: IViewerProps, prevState: IViewerState) {
    // console.log("\n componentDidUpdate ");
    // console.log("|||||||||||||||||||||||| prevProps===this.props " + (prevProps === this.props));
    // console.log("|||||||||||||||||||||||| prevState.detachCurrentImage " + prevState.detachCurrentImage);
    // console.log("|||||||||||||||||||||||| this.state.detachCurrentImage " + this.state.detachCurrentImage);

    //  If we detached current image, we have to show a new image
    if (this.state.detachCurrentImage) {
      // set timeout
      window.setTimeout(() => {
        // set the new state detachCurrentImage to false and show a new image
        this.setState({
          currentImage: this.props.image,
          detachCurrentImage: false,
        });
      }, 500);
    }
  }

  ///////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  /**
   * Hide main wrapper on tap (<= medium only).
   */
  handleTouchEnd() {
    if (breakpoints.active('<=medium')) {
      this.props.hideMain();
    }
  }

  /**
   * Touch gestures.
   */
  handleTouchStart(event: React.TouchEvent) {
    // Record start position.
    this.setState({
      touchPosX: event.touches[0].pageX,
      touchPosY: event.touches[0].pageY,
    });
  }

  /**
   * Touch gestures.
   */
  handleTouchMove(event: React.TouchEvent) {
    // No start position recorded? Bail.
    if (this.state.touchPosX === undefined || this.state.touchPosY === undefined) {
      return;
    }

    // Calculate stuff.
    let diffX = this.state.touchPosX - event.touches[0].pageX;
    let diffY = this.state.touchPosY - event.touches[0].pageY;
    let boundary = 20;
    let delta = 50;

    // Swipe left (next).
    if (diffY < boundary && diffY > -1 * boundary && diffX > delta) {
      this.props.handleNext();
    }

    // Swipe right (previous).
    else if (diffY < boundary && diffY > -1 * boundary && diffX < -1 * delta) {
      this.props.handlePrevious();
    }

    // Overscroll fix.
    let viewer = ReactDOM.findDOMNode(this.refs['viewer']) as HTMLDivElement;
    let th = viewer.offsetHeight;
    let ts = viewer.scrollHeight - viewer.scrollTop;

    if ((viewer.scrollTop <= 0 && diffY < 0) || (ts > th - 2 && ts < th + 2 && diffY > 0)) {
      event.preventDefault();
      event.stopPropagation();
    }
  }

  render() {
    // console.log("\n render");
    // console.log("|||||||||||||||||||||||| currentImage="+ (this.state.currentImage===null? "null":this.state.currentImage.h2));
    // console.log("|||||||||||||||||||||||| this.props.image="+ (this.props.image===null? "null":this.props.image.h2));
    // console.log("|||||||||||||||||||||||| detachCurrentImage=" + this.state.detachCurrentImage);

    let style = {
      width: this.props.width || '',
    };

    let slide =
      this.state.currentImage !== null ? (
        <Slide image={this.state.currentImage} onLock={this.props.onLock} detachCurrentImage={this.state.detachCurrentImage} />
      ) : (
        ''
      );

    return (
      <div
        id="viewer"
        ref="viewer"
        style={style}
        onTouchStart={this.handleTouchStart.bind(this)}
        onTouchEnd={this.handleTouchEnd.bind(this)}
        onTouchMove={this.handleTouchMove.bind(this)}
      >
        <div className="inner">
          <div className="nav-next" onClick={this.props.handleNext}></div>
          <div className="nav-previous" onClick={this.props.handlePrevious}></div>
          <Toggle hideMain={this.props.hideMain} showMain={this.props.showMain} />
        </div>
        {slide}
      </div>
    );
  }
}
