import * as React from 'react';
import { IImage } from '../IImage';

interface ISlideProps {
  image: IImage;
  onLock: (isLocked: boolean) => void;
  detachCurrentImage: boolean;
}

interface ISlideState {
  slideClass: string;
  isLoadingImage?: boolean;
}

export class Slide extends React.Component<ISlideProps, ISlideState> {
  constructor(props: any) {
    super(props);

    this.state = {
      slideClass: 'slide loading',
      isLoadingImage: true,
    };
  }

  // Before update component
  componentWillReceiveProps(nextProps: ISlideProps) {
    if (nextProps.image !== this.props.image) {
      this.setState({
        slideClass: 'slide loading',
      });
    }
  }

  render() {
    // Set background stuff
    let imgStyle = {
      backgroundImage: 'url(' + this.props.image.full + ')',
      backgroundPosition: this.props.image.position || 'center',
    };

    return (
      <div className={this.props.detachCurrentImage ? 'slide' : this.state.slideClass}>
        <div className="caption">
          <h2>{this.props.image.title}</h2>
          <p>{this.props.image.description}</p>
        </div>
        <div className="image" style={imgStyle}></div>
        <img src={this.props.image.full} onLoad={this.handleImageLoaded.bind(this)} style={{ display: 'none' }} />
      </div>
    );
  }

  handleImageLoaded() {
    window.setTimeout(() => {
      this.setState({
        slideClass: 'slide active',
        isLoadingImage: false,
      });
      this.props.onLock(false);
    }, 0);
  }
}

