import * as ReactDOM from 'react-dom';
import { Main } from './main/Main';
import { Viewer } from './viewer/Viewer';
import { IImage } from './IImage';
import { breakpoints } from '../libs/responsive-tools/breakpoints';
import { addEvent, removeClass, hasClass, addClass } from '../utils/helpers';

interface IGalleryProps {
  images: IImage[];
  settings: any;
}

interface IGalleryState {
  viewerWidth?: number;
  current: number | null; //Current slide index.
  locked: boolean; //Lock state.
  isDeactivateSlide?: boolean;
}

export class App extends React.Component<IGalleryProps, IGalleryState> {
  constructor(props: any) {
    super(props);
    this.state = {
      locked: false,
      current: null,
    };
  }

  componentDidMount() {
    // // IE<9: Fix viewer width (no calc support).
    // if (skel.vars.IEVersion < 9) {
    //     let windowIE8:any = window;
    //     windowIE8.attachEvent('resize', this.handleResize);
    // }

    // Initial slide.
    window.setTimeout(() => {
      // // Show first slide if xsmall isn't active or it just deactivated.
      // skel.on('-xsmall !xsmall', () => {
      //     if (this.state.current === null) {
      //         this.switchTo(0, true);
      //     }
      // });

      // Show first slide if xsmall isn't active.
      breakpoints.on('>xsmall', () => {
        if (this.state.current === null) this.switchTo(0, true);
      });
    }, 0);

    /*
     * Keyboard shortcuts.
     */
    let keys: { [n: number]: () => void } = {
      // Escape: Toggle main wrapper.
      27: () => {
        this.handleToggle();
      },

      // Up: Move up.
      38: () => {
        this.handleUp();
      },

      // Down: Move down.
      40: () => {
        this.handleDown();
      },

      // Space: Next.
      32: () => {
        this.handleNext();
      },

      // Right Arrow: Next.
      39: () => {
        this.handleNext();
      },

      // Left Arrow: Previous.
      37: () => {
        this.handlePrevious();
      },
    };

    // Find Elements IE8+
    let elements = document.body.querySelectorAll('input,select,textarea');
    elements.forEach((
      el // Ignore shortcuts within form elements.
    ) =>
      addEvent(el, 'keydown', function (event) {
        event.stopPropagation();
      })
    );

    // Keyboard shortcuts. Add event to window.
    addEvent(window, 'keydown', function (event: Event | KeyboardEvent) {
      // Ignore if xsmall is active.
      if (breakpoints.active('<=xsmall')) return;

      // Check keycode.
      if ('keyCode' in event && event.keyCode in keys) {
        // Stop other events.
        event.stopPropagation();
        event.preventDefault();

        // Call shortcut.
        keys[event.keyCode]();
      }

      // // Ignore if xsmall is active.
      // if (breakpoint('xsmall').active) return;

      // // Check keycode.
      // if (event.keyCode in keys) {
      //   // Stop other events.
      //   event.stopPropagation();
      //   event.preventDefault();
      //   // Call shortcut.
      //   keys[event.keyCode]();
      // }
    });
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  render() {
    let image: IImage = this.props.images[this.state.current || 0];

    return (
      <div>
        <Main
          images={this.props.images}
          isLocked={this.state.locked}
          current={this.state.current}
          ref="main"
          onThumbnailClick={this.handleThumbnailClick.bind(this)}
          hideMain={this.hideMain.bind(this)}
          showMain={this.showMain.bind(this)}
          settings={this.props.settings}
        />
        <Viewer
          width={this.state.viewerWidth}
          image={image}
          onLock={this.handleLock.bind(this)}
          isLocked={this.state.locked}
          hideMain={this.hideMain.bind(this)}
          showMain={this.showMain.bind(this)}
          handleNext={this.handleNext.bind(this)}
          handlePrevious={this.handlePrevious.bind(this)}
        />
      </div>
    );
  }

  ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

  handleResize() {
    const mainEl = ReactDOM.findDOMNode(this.refs['main']);
    if (mainEl && 'offsetWidth' in mainEl) {
      let mainWidth = mainEl['offsetWidth']; // ReactDOM.findDOMNode(this).clientWidth;// this.refs['main'].getDOMNode().offsetWidth;
      window.setTimeout(() => {
        this.setState({
          viewerWidth: window.innerWidth - mainWidth,
        });
      }, 100);
    }
  }

  handleLock(isLocked: boolean) {
    this.setState({
      locked: isLocked,
    });
  }

  handleThumbnailClick(index: number) {
    // Switch to this thumbnail's slide.
    this.switchTo(index);
  }

  /**
   * Switch to a specific slide.
   */
  switchTo(index: number, noHide?: boolean) {
    // Already at index and xsmall isn't active? Bail.
    if (this.state.current === index && !breakpoints.active('<=xsmall')) return;

    // Locked? Bail.
    if (this.state.locked) {
      return;
    }

    // Hide main wrapper if medium is active.
    if (!noHide && breakpoints.active('<=medium')) this.hideMain();

    // window.setTimeout(() => {
    // Update current.
    this.setState({
      current: index,
      locked: true,
    });
    // }, 100);
  }

  /**
   * Hides the main wrapper.
   */
  hideMain() {
    removeClass(document.body, 'is-loading-0');

    // Already hidden? Bail.
    if (hasClass(document.body, 'fullscreen')) {
      return;
    }

    // Hide main wrapper.
    addClass(document.body, 'fullscreen');

    // Blur.
    const mainEl = ReactDOM.findDOMNode(this.refs['main']) as HTMLElement;
    if(mainEl){
      mainEl.blur();
    }
  }

  /**
   * Shows the main wrapper.
   */
  showMain() {
    // Already visible? Bail.
    if (!hasClass(document.body, 'fullscreen')) {
      return;
    }

    // Show main wrapper.
    removeClass(document.body, 'fullscreen');

    // Focus.
    (ReactDOM.findDOMNode(this.refs['main']) as HTMLDivElement).focus();
  }

  /**
   * Switches to the next slide.
   */
  handleNext() {
    // Calculate new index.
    let i;
    let c = this.state.current || 0;
    let l = this.props.images.length;

    if (c >= l - 1) i = 0;
    else i = c + 1;

    // Switch.
    this.switchTo(i);
  }

  /**
   * Switches to the previous slide.
   */
  handlePrevious() {
    // Calculate new index.
    let i;
    let c = this.state.current || 0;
    let l = this.props.images.length;

    if (c <= 0) i = l - 1;
    else i = c - 1;

    // Switch.
    this.switchTo(i);
  }

  /**
   * Switches to slide "below" current.
   */
  handleDown() {
    // Fullscreen? Bail.
    if (hasClass(document.body, 'fullscreen')) {
      return;
    }

    // Calculate new index.
    let i;
    let c = this.state.current || 0;
    let l = this.props.images.length;
    let tpr = this.props.settings.thumbnailsPerRow;

    if (c >= l - tpr) i = c - l + tpr;
    else i = c + tpr;

    // Switch.
    this.switchTo(i);
  }

  /**
   * Switches to slide "above" current.
   */
  handleUp() {
    // Fullscreen? Bail.
    if (hasClass(document.body, 'fullscreen')) {
      return;
    }

    // Calculate new index.
    let i;
    let c = this.state.current || 0;
    let l = this.props.images.length;
    let tpr = this.props.settings.thumbnailsPerRow;

    if (c <= tpr - 1) i = l - (tpr - 1 - c) - 1;
    else i = c - tpr;

    // Switch.
    this.switchTo(i);
  }

  /**
   * Toggles main wrapper.
   */
  handleToggle() {
    if (hasClass(document.body, 'fullscreen')) {
      this.showMain();
    } else {
      this.hideMain();
    }
  }
}
