import * as React from 'react';
import {Thumbnail} from './Thumbnail';
import {IImage} from '../IImage';

interface IThumbnailsProps {
    images: IImage[];
    current: number | null;
    isLocked: boolean;
    onThumbnailClick: (index:number) => void;
}


export class Thumbnails extends React.Component<IThumbnailsProps, any>{
    render() {
        let thumbnails: JSX.Element[] = [];
        this.props.images.forEach((image, index)=>{
            thumbnails.push(
                <Thumbnail image={image} index={index} key={index} isActive={this.props.current===index} isLocked={this.props.isLocked} onThumbnailClick={this.props.onThumbnailClick}/>
            );
        });
        return (
            <section id="thumbnails">
                {thumbnails}
            </section>
        );
    }
}